.profile__title {
  font-weight: 500;
  font-size: 42px;
  line-height: 56px;
  margin: 0;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}

@media (max-width: 767px) {
  .profile__title {
    font-size: 27px;
    line-height: 56px;
    text-align: center;
  }
}